body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
  }
  
  .chat-container {
    width: 400px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  
  .chat-box {
    height: 400px;
    overflow-y: auto;
    padding: 15px;
    display: flex;
    flex-direction: column;
  }
  
  .message {
    padding: 10px;
    border-radius: 8px;
    margin: 5px 0;
    max-width: 80%;
  }
  
  .user {
    background: #007bff;
    color: white;
    align-self: flex-end;
  }
  
  .ai {
    background: #e0e0e0;
    color: black;
    align-self: flex-start;
  }
  
  .input-box {
    display: flex;
    border-top: 1px solid #ddd;
    padding: 10px;
  }
  
  input {
    flex: 1;
    padding: 10px;
    border: none;
    outline: none;
    border-radius: 5px;
  }
  
  button {
    background: #007bff;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 5px;
  }